import React, { useState, useEffect } from 'react'
import firebase from '../database'
import { Link, useParams } from 'react-router-dom'

const Post = () => {
  const { id } = useParams()
  const [details, setDetails] = useState([])
  const ref = firebase.firestore().collection('details')

  function getDetails() {
    ref.doc(id).onSnapshot((querySnapshot) => {
      setDetails(querySnapshot.data())
    })
  }
  useEffect(() => {
    getDetails()
    // eslint-disable-next-line
  }, [])
  console.log(details.title);

  var today = new Date(details.time)
  var dd = String(today.getDate()).padStart(2, '0')
  var mm = String(today.getMonth() + 1).padStart(2, '0') //January is 0!
  var yyyy = today.getFullYear()
  today = dd + '/' + mm + '/' + yyyy

  return (
    <div className='card text-center hs-bg'>
      <div className='card-header h1'></div>
      <div className='card-body'>
        <h5 className='card-title'>{details.title}</h5>
        <p className='card-text text-dark'>{details.content}</p>
        <Link to='/blog' className='btn'>
          Back To Blog
        </Link>
      </div>
      <div className='card-footer text-muted'> Updated on {today}</div>
    </div>
  )
}


export default Post
