import React from 'react'
import { Link } from 'react-router-dom'
import './About-Components/AboutC.css'

const About = () => {
  return (
    <div className='row justify-content-center'>
      <div className='card  ab-one'>
        <img
          className='card-img-top'
          src='https://i.pinimg.com/originals/df/25/e3/df25e39d7400f1bbcb7adf695063faaa.jpg'
          alt='Card image cap'
        />
        <div className='card-body'>
          <h3 className='card-title'>MEVIN SUSHIL P</h3>
        </div>
      </div>

      <div className='card ab-two'>
        <div className='card-body'>
          <h5 className='card-title'>About Me</h5>
          <p className='card-text text-left'>
            I am Mevin Sushil P working as a Primary Teacher in BEM UP School
            Parapperi Living in Parapperi, Tirur, Malappuram District.
          </p>
          <Link to='/blog' className='btn'>
            Read my Posts
          </Link>
        </div>
      </div>
    </div>
  )
}

export default About
