import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import firebase from '../database'
import './Home-Components/HomeC.css'

var today = new Date()
var dd = String(today.getDate()).padStart(2, '0')
var mm = String(today.getMonth() + 1).padStart(2, '0') //January is 0!
var yyyy = today.getFullYear()
today = mm + '/' + dd + '/' + yyyy

// const [blog, setBlog] = useState(data)

const Blog = () => {
  const [details, setDetails] = useState([])
  const ref = firebase.firestore().collection('details')

  function getDetails() {
    ref.onSnapshot((querySnapshot) => {
      const items = []
      querySnapshot.forEach((doc) => {
        items.push(doc)
      })
      setDetails(items)
    })
  }

  useEffect(() => {
    getDetails()
    // eslint-disable-next-line
  }, [])
  return (
    <div>
      <div className='mb-4  full-bg'>
        <h1 className='display-4 text-white'>Blog</h1>
        <p className='lead text-warning '>
          Whatever you do, do it well. â€“ Walt Disney
        </p>
      </div>
      <div className='card-deck row row-cols-1 row-cols-md-3 mt-4 '>
        {details.map((doc) => {
          const date1 = new Date(doc.data().time)
          const date2 = new Date(today)
          const diffTime = Math.abs(date2 - date1)
          var diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24))
          if (diffDays === 0) {
            diffDays = 'Today'
          } else {
            diffDays = diffDays + ' days ago'
          }
          function truncate(str, no_words) {
            return str.split(' ').splice(0, no_words).join(' ') + ' ...'
          }

          // Example
          const dbContent = truncate(doc.data().content, 4)
          return (
            <div key={doc.id} className='col col-sm-6 col-md-4 col-lg-3 mb-4'>
              <div className='card h-100 text-dark text-center c-bg border-dark mb-3 ps-bg'>
                <div className='card-body'>
                  <h5 className='card-title'>{doc.data().title}</h5>
                  <hr />
                  <p className='card-text text-dark '>{dbContent}</p>
                  <Link to={`/post/${doc.id}`} className='btn btn-dark'>
                    Read More
                  </Link>
                </div>
                <div className='card-footer'>
                  <small className='text-dark'>Updated {diffDays}</small>
                </div>
              </div>
            </div>
          )
        })}
        {/* <div className='col col-sm-6 col-md-4 col-lg-3 mb-4'>
          <div className='card h-100 text-dark text-center c-bg border-dark mb-3 ps-bg'>
            <div className='card-body'>
              <h3 className='card-title'>Home</h3>
              <hr />
              <p className='card-text text-dark h6'>My Home Page</p>
              <Link to='/' className='btn btn-dark'>
                Go to Home
              </Link>
            </div>
            <div className='card-footer'></div>
          </div>
        </div> */}
      </div>
    </div>
  )
}

export default Blog
